export default function Home() {
  return (
    <div style={{
      background: "linear-gradient(135deg, #1a1a2e 0%, #16213e 100%)",
      minHeight: "100vh",
      padding: "40px 20px"
    }}>
      <div style={{ maxWidth: "1200px", margin: "0 auto" }}>
        
        {/* HERO SECTION */}
        <div style={{ textAlign: "center", marginBottom: "60px" }}>
          <h1 style={{ 
            color: "white", 
            fontSize: "48px",
            marginBottom: "20px",
            background: "linear-gradient(45deg, #ff6b6b, #4ecdc4)",
            WebkitBackgroundClip: "text",
            WebkitTextFillColor: "transparent"
          }}>
            Anime Explorer
          </h1>
          <p style={{ color: "#aaa", fontSize: "20px", marginBottom: "40px" }}>
            Descubra, explore e acompanhe seus animes favoritos
          </p>
          
          {/* Mini busca */}
          <div style={{ maxWidth: "600px", margin: "0 auto" }}>
            <input 
              placeholder="Buscar anime..."
              style={{
                width: "100%",
                padding: "15px 20px",
                borderRadius: "50px",
                border: "none",
                fontSize: "16px",
                boxShadow: "0 10px 30px rgba(0,0,0,0.3)"
              }}
            />
          </div>
        </div>

        {/* QUICK ACTIONS */}
        <div style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))",
          gap: "20px",
          marginBottom: "60px"
        }}>
          <div style={{
            background: "rgba(255,255,255,0.05)",
            padding: "30px",
            borderRadius: "15px",
            border: "1px solid rgba(255,255,255,0.1)",
            textAlign: "center",
            cursor: "pointer",
            transition: "all 0.3s"
          }}
          onMouseEnter={(e) => e.currentTarget.style.transform = "translateY(-10px)"}
          onClick={() => router.push('/search')}
          >
            <div style={{ fontSize: "40px", marginBottom: "15px" }}>üîç</div>
            <h3 style={{ color: "white", marginBottom: "10px" }}>Buscar Animes</h3>
            <p style={{ color: "#aaa" }}>Encontre por nome, g√™nero ou temporada</p>
          </div>

          <div style={{
            background: "rgba(255,255,255,0.05)",
            padding: "30px",
            borderRadius: "15px",
            border: "1px solid rgba(255,255,255,0.1)",
            textAlign: "center",
            cursor: "pointer",
            transition: "all 0.3s"
          }}
          onMouseEnter={(e) => e.currentTarget.style.transform = "translateY(-10px)"}
          onClick={() => router.push('/season')}
          >
            <div style={{ fontSize: "40px", marginBottom: "15px" }}>üìÖ</div>
            <h3 style={{ color: "white", marginBottom: "10px" }}>Temporada Atual</h3>
            <p style={{ color: "#aaa" }}>Veja os lan√ßamentos deste season</p>
          </div>

          <div style={{
            background: "rgba(255,255,255,0.05)",
            padding: "30px",
            borderRadius: "15px",
            border: "1px solid rgba(255,255,255,0.1)",
            textAlign: "center",
            cursor: "pointer",
            transition: "all 0.3s"
          }}
          onMouseEnter={(e) => e.currentTarget.style.transform = "translateY(-10px)"}
          onClick={() => router.push('/top')}
          >
            <div style={{ fontSize: "40px", marginBottom: "15px" }}>‚≠ê</div>
            <h3 style={{ color: "white", marginBottom: "10px" }}>Top Animes</h3>
            <p style={{ color: "#aaa" }}>Os mais bem avaliados de todos os tempos</p>
          </div>
        </div>

        {/* CTA FINAL */}
        <div style={{
          textAlign: "center",
          padding: "40px",
          background: "linear-gradient(45deg, rgba(30,136,229,0.1), rgba(255,107,107,0.1))",
          borderRadius: "20px",
          border: "1px solid rgba(255,255,255,0.05)"
        }}>
          <h2 style={{ color: "white", marginBottom: "20px" }}>Pronto para explorar?</h2>
          <button
            onClick={() => router.push('/search')}
            style={{
              padding: "15px 40px",
              background: "linear-gradient(45deg, #1e88e5, #ff6b6b)",
              color: "white",
              border: "none",
              borderRadius: "50px",
              fontSize: "18px",
              fontWeight: "bold",
              cursor: "pointer",
              transition: "all 0.3s"
            }}
            onMouseEnter={(e) => e.currentTarget.style.transform = "scale(1.05)"}
          >
            Come√ßar a Buscar ‚Üí
          </button>
        </div>

      </div>
    </div>
  );
}